.our-commitment {
  max-width: 1200px;
  margin: 0 auto;
  padding: 25vh 20px 20px;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

.commitment-header {
  text-align: center;
  margin-bottom: 40px;
}

.commitment-header h1 {
  color: #333;
  margin-bottom: 10px;
  font-size: 2.5rem;
}

.commitment-header p {
  color: #666;
  font-size: 1.2rem;
}

.commitment-content {
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  display: flex;
}

.commitment-image {
  text-align: center;
  flex: 40%;
}

.commitment-image img {
  border-radius: 8px;
  max-width: 100%;
  height: auto;
  box-shadow: 0 4px 8px #0000001a;
}

.commitment-details {
  flex: 55%;
}

.commitment-details h2 {
  color: #333;
  border-bottom: 2px solid #007bff;
  margin-top: 20px;
  margin-bottom: 10px;
  padding-bottom: 5px;
  font-size: 1.8rem;
}

.commitment-details p {
  color: #555;
  margin-bottom: 15px;
  font-size: 1rem;
}

@media (width <= 768px) {
  .our-commitment {
    padding: 10px;
  }

  .commitment-header h1 {
    font-size: 2rem;
  }

  .commitment-header p {
    font-size: 1rem;
  }

  .commitment-content {
    flex-direction: column;
  }

  .commitment-image, .commitment-details {
    flex: 100%;
  }

  .commitment-details h2 {
    font-size: 1.5rem;
  }
}

@media (width <= 480px) {
  .commitment-header h1 {
    font-size: 1.8rem;
  }

  .commitment-details h2 {
    font-size: 1.3rem;
  }

  .commitment-details p {
    font-size: .9rem;
  }
}
